.footer {
    padding: 70px 0;
    box-shadow: $box-shadow;
    &__logo {
        height: 38px;
    }
    &__about {
        p {
            margin-bottom: 2rem;
        }
    }
    &__title {
        text-transform: uppercase;
        margin-bottom: 2rem;
        font-weight: 600;
    }
    &__content {
        p {
            margin-bottom: 1.25rem;
        }
    }
}